<template>
 <transition name="move">
  <div v-show="showFlag" class="food" ref="food-depart">
    <div class="food-content">
      <div class="image-header">
      	<img :src="food.image">
      	<div class="back" @click="hide">
      	<i class="icon-arrow_lift"></i>
      	</div>
      </div>
      <div class="content">
      	<h1 class="title">{{food.name}}</h1>
      	<div class="detail">
      		<span class="sell-count">{{food.sellCount}}</span>
      		<span class="rating">{{food.rating}}%</span>
      	</div>
      	<div class="price">
      		<span class="now">{{food.price}}</span><span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
      	</div>
        <div class="cartcontrol-wrapper">
      	  <control :food="food"></control>
        </div>
        <transition name="fade">
          <div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count===0"></div>
        </transition>
      </div>
      <split v-show="food.info"></split>
      <div class="info" v-show="food.info">
      	<h1 class="food-title"></h1>
      	<p class="food-text">{{food.info}}</p>
      </div>
      <split></split>
      <div class="food-rating">
      	<h1 class="rating-title"></h1>
      	<ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
      	<div class="rating-wrapper">
      		<ul v-show="food.ratings && food.ratings.length">
      			<li v-show="needShow(rating.rateType, rating.text)" v-for="(rating, index) in food.ratings" class="rating-item border-1px">
      				<div class="user">
      					<span class="user-name">{{rating.username}}</span>
      					<img class="user-avatar" width="12" height="12" :src="rating.avatar">
      				</div>
      				<div class="time">{{rating.rateTime | formatDate}}</div>
      				<p class="text">
      					<span :class="{'icon-thumb_up':rating.rateType===0, 'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
      				</p>
      			</li>
      		</ul>
      		<div class="no-rating" v-show="!food.ratings || !food.ratings.length"></div>
      	</div>
      </div>
    </div>
  </div>
 </transition>
</template>

<script>
import Vue from 'vue';
import BScroll from 'better-scroll';
import Control from '../carcontrol/Control';
import Bus from '../../common/js/eventBus.js';
import Split from '../split/Split';
import Ratingselect from '../ratingselect/Ratingselect';
import {format} from '../../common/js/date.js';

// const POSITIVE = 0;
// const NEGATIVE = 1;
const ALL = 2;
export default {
  name: 'food',
  components: {
      Control,
      Split,
      Ratingselect
  },
  data () {
    return {
    	showFlag: false,
    	selectType: ALL,
    	onlyContent: true,
    	desc: {
    		all: '',
    		positive: '',
    		negative: ''
    	}
    };
  },
  props: {
  	food: {
  		type: Object
  	}
  },
  filters: {
    formatDate (time) {
        let date = new Date(time);
        return format(date, 'yyyy-MM-dd hh:mm');
    }
  },
  created () {
  	 this.$nextTick(() => {
  	 	this.scroll = new BScroll(this.$refs['food-depart'], {click: true});
  	 });
      Bus.$on('ratingtype-select', type => {
  	   	  this.selectType = type;
  	   	  this.$nextTick(() => {
  	   	  	this.scroll.refresh();
  	   	  });
  	   });
      Bus.$on('content-toggle', onlyContents => {
  	   	  this.onlyContent = onlyContents;
  	   	  this.$nextTick(() => {
  	   	  	this.scroll.refresh();
  	   	  });
  	   });
  },
  methods: {
  	show () {
  		this.selectType = ALL;
  		this.onlyContent = true;
  		this.showFlag = true;
  		this.$nextTick(() => {
  			if (!this.scroll) {
  				console.log(11);
  				this.scroll = new BScroll(this.$refs['food-depart'], {click: true});
  			} else {
  				this.scroll.refresh();
  			}
  		});
  	},
  	hide () {
  		if (!event._constructed) {
  			return;
  		}
  		this.showFlag = false;
  	},
  	addFirst () {
        if (!event._constructed) {
  			return;
  		}
  		console.log(event.target);
  		Bus.$emit('cart-add', event.target);
         Vue.set(this.food, 'count', 1);
  	},
  	needShow (type, text) {
        if (this.onlyContent && !text) {
        	return false;
        }
        if (this.selectType === ALL) {
        	return true;
        } else {
        	return type === this.selectType;
        }
  	}
  }
};
</script>

<style scoped>
.food{
	position: fixed;
	left: 0;
	top: 0;
	bottom: 48px;
	z-index: 30;
	width: 100%;
	background: #fff;
	transform: translate3d(0, 0, 0);
}
.move-enter-active,.move-leave-active{
	transition: all 0.2s linear;
}
.move-enter,.move-leave-active{
	transform: translate3d(100%, 0, 0);
}
.image-header{
	position: relative;
	width: 100%;
	height: 0;
	padding-top: 100%;
}
.image-header img{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
.back{
	position: absolute;
	top: 10px;
	left: 0
}
.icon-arrow_lift{
	display: block;
	padding: 10px;
	font-size: 20px;
	color: #fff;
}
.content{
	position: relative;
	padding: 18px;
}
.content .title{
	line-height: 14px;
	margin-bottom: 8px;
	font-size: 14px;
	font-weight: 700;
	color: rgb(7, 17, 27);
}
.content .detail{
	margin-bottom: 18px;
	line-height: 10px;
	font-size: 0;
	height: 10px;
}
.sell-count,.rating{
	font-size: 10px;
	color: rgb(147, 153, 159);
}
.sell-count{
	margin-right: 12px;
}
.price{
	font-weight: 700;
	line-height: 24px;
}
.price .now{
	margin-right: 8px;
	font-size: 14px;
	color: rgb(240, 20, 20);
}
.price .old{
	text-decoration: line-through;
	font-size: 10px;
	color: rgb(147, 153, 159);
}
.cartcontrol-wrapper{
	position: absolute;
	right: 12px;
	bottom: 12px;
}
.buy{
	position: absolute;
	right: 18px;
	bottom: 18px;
	z-index: 10;
	height: 24px;
	line-height: 24px;
	padding: 0 12px;
	box-sizing: border-box;
	font-size: 10px;
	border-radius: 12px;
	text-align: center; 
	color: #fff;
	background: rgb(0, 160, 220);
}
.fade-enter-active,.fade-leave-active{
	transition: all 0.2s;
}
.fade-enter,.fade-leave-active{
	opacity: 0;
}
.info{
	padding: 18px;
}
.info .food-title{
	line-height: 14px;
	margin-bottom: 6px;
	font-size: 14px;
	color: rgb(7, 17, 27);
}
.info .food-text{
	line-height: 24px;
	padding: 0 8px;
	font-size: 12px;
	color: rgb(77, 85, 93);
}
.food-rating{
	padding-top: 18px;
}
.rating-title{
	line-height: 14px;
	margin-left: 18px;
	font-size: 14px;
	color: rgb(7, 17, 27);
}
.rating-wrapper{
	padding: 0 18px;
}
.rating-item{
	position: relative;
	padding: 16px 0;
}
.rating-item:after{
   display: block;
   position: absolute;
   left: 0;
   bottom: 0;
   width: 100%;
   border-top: 1px solid rgba(7,17,27,0.1);
   content: ''; 
}
.rating-item:last-child:after{
    display: none;
}
.user{
	position: absolute;
	right: 0;
	top: 16px;
	line-height: 12px;
	font-size: 0;
}
.user-name{
	display: inline-block;
	vertical-align: top;
	margin-right: 6px;
	font-size: 10px;
	color: rgb(147, 153, 159); 
}
.user-avatar{
	border-radius: 50%;
}
.time{
	margin-bottom: 6px;
	line-height: 12px;
	font-size: 10px;
	color: rgb(147, 153, 159);
}
.text{
	line-height: 16px;
	font-size: 12px;
	color: rgb(7, 17, 27);
}
.icon-thumb_up,.icon-thumb_down{
	margin-right: 4px;
	line-height: 16px;
	font-size: 12px;
}
.icon-thumb_up{
	color: rgb(0, 160, 220);
}
.icon-thumb_down{
    color: rgb(147, 153, 159);
}
.no-rating{
	padding: 16px 0;
	font-size: 12px;
	color: rgb(147, 153, 159);
}
@media (-webkit-min-device-pixel-ratio: 1.5),(min-device-pixel-ratio: 1.5){
  .border-1px::after{
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
  }
}

@media (-webkit-min-device-pixel-ratio: 2),(min-device-pixel-ratio: 2){
  .border-1px::after{
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
  }
}
</style>



